.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding-inline: 16px;
  font-family: Roboto, serif;
}

.card__image-container {
  justify-content: center;
  margin-block: 32px 40px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price-container {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-link {
  text-transform: uppercase;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: inline-block;
}

.card__button {
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
  padding-block: 12px;
  display: flex;
}

.card__button:hover {
  background-color: #0000;
  border: 1px solid #00acdc;
}

.card__button:hover .card__buy-link {
  color: #00acdc;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.stars {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
